* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

body {
    font-family: "Segoe UI", Arial, Helvetica, sans-serif;
    line-height: 1.4;
    background-color: #fcfcfc;
    color: #1f1f1f;
}

.container{
    max-width: 500px;
    margin: 20px auto;
    /* border: 1px solid #5fbdb6; */
}

.header-container{
    background-color: #5db9b1;
    color: #fff;
    padding: 10px 15px;
    border-radius: 10px;
}
.header-title{
    font-size: 25px;
    line-height: 1.447em;
    margin: 0;
    text-align: center;
}

.todo-item{
    list-style: none;
    padding: 10px 15px;
    /* border: 1px solid #5fbdb6; */
}
.todo-item input {
    margin-right:12px ;
}

.completed {
    font-size: italic;
    color: #c5e2d2;
    text-decoration: line-through;
}

.btn-style {
    background: #cb7d5b;
    color: #fff;
    border: 1px solid #e27b36;
    padding: 2px 5px;
    border-radius: 50%;
    cursor: pointer;
    float: right;
    outline: none;
}


.form-container{
    padding-top: 3px;
    display: flex;
    width: 100%;
}
.input-text{
    flex: 7;
    font-size: 14px;
    padding: 6px 15px;
    background: rgba(91, 181, 173, 0.55);
    border: none;
    color: #fff;
    outline: none;
    font-weight: 400;
    width: 80%;
    border-radius: 7px;
}
.input-text::placeholder {
    color: #fff;
    opacity: 1;
}

.input-submit{
    flex: 1;
    border: none;
    background: #76bab5;
    color: #fff;
    text-transform: uppercase;
    cursor: pointer;
    font-weight: 600;
    width: 20%;
    outline: none;
    border-radius: 7px;
    margin-left: 4px;
}

.todo-item span {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    width: 80%;
    height: 20px;
    display: inline-block;
}